import { useState, useEffect } from 'react'
import Icon from '../../components/Icon/Icon'
import Button from '../../components/Button/Button'
import apiProxy from '../../model/ApiProxy'
import { useAlert } from 'react-alert'
import './faucet.less'
import { inject, observer } from 'mobx-react'
function Faucet({ wallet, lang }) {
  const tonkenList = [
    {
      token: 'BUSD'
    },
    {
      token: 'SXP'
    },
    {
      token: 'ETH'
    },
    {
      token: 'LTC'
    },
    {
      token: 'XRP'
    },
    {
      token: 'BTCB'
    },
    {
      token: 'ADA'
    },
    {
      token: 'CAKE'
    },
    {
      token: 'MATIC'
    },
    {
      token: 'AAVE'
    },
    {
      token: 'TUSD'
    },
    {
      token: 'TRX'
    },
    {
      token: 'USDT'
    },
    {
      token: 'USDC'
    },
    {
      token: 'UST'
    },
    {
      token: 'LUNA'
    },
    {
      token: 'DOGE'
    },
  ]
  const arbiList = [
    {
      token: "USDC"
    },
    // {
    //   token: "USDT"
    // },
    {
      token: "LINK"
    },
    {
      token: "DAI"
    },
    {
      token: "AAVE"
    },
    {
      token: "WBTC"
    },
  ]
  const alert = useAlert();
  const [showList, setShowList] = useState(tonkenList)
  const [check, setCheck] = useState("BSC")
  const mint = async (token) => {
    if (wallet.isConnected()) {
      if (check === 'BSC' && +wallet.detail.chainId === 97) {
        let param = [wallet.detail.chainId, wallet.detail.account, token]
        let res = await apiProxy.request('mintTokenV3', param, { includeResponse: true })
        if (!res || !res.success) {
          alert.error('mint fail')
        } else {
          alert.success('mint success')
        }
      } else if (check === 'ARBITRUM' && (+wallet.detail.chainId === 421611 || +wallet.detail.chainId === 421613)) {
        let param = [wallet.detail.chainId, wallet.detail.account, token]
        let res = await apiProxy.request('mintTokenV3', param, { includeResponse: true })
        if (!res || !res.success) {
          alert.error('mint fail')
        } else {
          alert.success('mint success')
        }
      }

    } else {
      alert.error('Please connect wallet')
    }

  }
  useEffect(() => {
    if (check === 'BSC') {
      setShowList(tonkenList)
    } else {
      setShowList(arbiList)
    }
  }, [check])
  return (
    <div className='testnet-faucet'>
      <div className='faucet-info'>
        <div className='faucet-title'>
          {lang['obtain-testnet-token']}
        </div>
        <div className='tab'>
          <div className={check === "BSC" ? 'tab-bsc check' : 'tab-bsc'} onClick={() => setCheck('BSC')}>BSC</div>
          <div className={check === "ARBITRUM" ? 'tab-arbitrum check' : 'tab-arbitrum'} onClick={() => setCheck('ARBITRUM')}>ARBITRUM</div>
        </div>
        <div className='mint-bnb'>
          <div>
            {check === "BSC" ? lang['go-to-min-bnb'] : lang['go-to-min-eth']}
          </div>
          {check === 'BSC' && <div className='bnb-button'>
            <a rel='noreferrer' target='_blank' href='https://testnet.binance.org/faucet-smart'>
              BNB
            </a>
          </div>}
          {check === 'ARBITRUM' && <div className='bnb-button'>
            <a rel='noreferrer' target='_blank' href='https://rinkebyfaucet.com/'>
              ETH
            </a>
          </div>}

        </div>
        <div className='mint-token'>
          {showList.map((item, index) => {
            return (
              <div className='mint-token-box' key={index}>
                <div className='token-logo'>
                  <Icon token={item.token} width={60} height={60} />
                </div>
                <div className='token-name'>
                  {item.token}
                </div>
                <div className='token-button'>
                  <Button fontWeight={300} label={lang['mint']} width={88} height={31} onClick={() => mint(item.token)} />
                </div>
              </div>
            )
          })}
        </div>

      </div>

    </div>
  )
}
export default inject('wallet')(observer(Faucet))